@import url('https://fonts.googleapis.com/css?family=Open+Sans');
$pink:#FD466A;
$yellow:#ffa150  ;
$fsize:18px;

$fontfam:'Open Sans', sans-serif;
.fetch_database_style{
    background: #fff;
    border: 1px solid $yellow;
    max-width: 600px;
    padding: 20px;
    margin: 0 auto;
    position: absolute;
    /* display: flex; */
    /* flex-basis: auto; */
    left: 0;
    right: 0;
    top: 80px;
    font-family: $fontfam;
    font-size: $fsize;

a,input,button{
    outline:0;
    border: 0;
}
    h2{
        text-align: center;
        text-transform: capitalize;
        font-size: 1.5rem;
        color: #444;


    }

    form{ margin-top: 20px;
       #inputup{
           margin-bottom: 20px;
        input,select{    border: 1px solid #ddd;
            height: 35px;
            width: 100%;

        }
        span{
            font-size: 0.9rem;
            margin-bottom: 10px;
        }
       }

       
    }

    button,a{
        
        border-radius: .7rem;
        padding: 0 20px;
        line-height: 30px;
        -webkit-transition: background-color .4s,border-color .4s;
        transition: background-color .4s,border-color .4s;
        border: 1px solid $yellow;
        background: transparent;
        margin: 0 auto;
        max-width: 100%;
        margin-top: 20px;
        width: 250px;
        display: block;
        color: $yellow;    text-align: center;
        
       }
       

}


.tabelContainer{
    max-width: 100%;
    width: 99%;
    overflow-y: scroll;
}

.tablenav-pages{
    span{
        color: $yellow; 
    }
    a{    background: transparent;color: $yellow!important; 
        &:active{
            color: $yellow; 
        }

        &:hover{
            border-color: $yellow!important; 
            color: $yellow!important; 
            background: transparent!important; 
        }
    }
}